import React, { useEffect, useRef, useState } from "react";
import { Button, Card, Col, Form, Row } from "react-bootstrap";
import { BiSearch } from "react-icons/bi";
import { AiFillEye } from "react-icons/ai";
import { Link, useNavigate } from "react-router-dom";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import { getPrescriptionList } from "../../../app/features/patientPrescription/patientPrescription.slice";
import { useDispatch, useSelector } from "react-redux";
import moment from "moment";
import { PATH } from "../../../utils/path";
import MedicationModel from "../Prescription/MedicationModel";
import PrintComponents from "react-print-components";
import { PiDownloadSimpleBold } from "react-icons/pi";
import PrescriptionPDF from "../../../components/Theme/PrescriptionPDF";

export default function CurrentPrescriptionDatatable() {
  const [searchValue, setSearchValue] = useState();
  const [show, setShow] = useState(false);
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const patientApt = useSelector((state) => state?.patientPrescription);
  const { user } = useSelector((state) => state.auth);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const [viewPresctionData, setViewPresctionData] = useState();
  const printButtonRef = useRef(null);

  const printTimeOut = (data) => {
    setShow(false);
    setTimeout(function () {
      printButtonRef?.current?.click();
    }, 1000);
  };

  const viewPrescription = (data) => {
    handleShow();
    setViewPresctionData(data);
  };
  useEffect(() => {
    const finalData = {
      patientId: user?.userId,
      search: searchValue,
    };
    dispatch(getPrescriptionList({ finalData }));
  }, [dispatch, searchValue, user?.userId]);

  const columns = [
    {
      dataField: "prescriptionId",
      text: "ID",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "doctorName",
      text: "Doctor Name",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "medicine",
      text: "Medicine",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (text) => (text ? text : "N/A"),
    },
    {
      dataField: "prescribedDate",
      text: "Prescribed Date",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (row, col) => {
        return (
          <>
            <p>{moment(col?.prescribedDate).format("MM/DD/YYYY")}</p>
          </>
        );
      },
    },
    {
      dataField: "action",
      text: "Actions",
      sort: false,
      formatter: (row, data) => (
        <Link className="mx-2">
          <AiFillEye
            className="me-2"
            style={{ color: "#3F8BFC" }}
            size={18}
            onClick={() => viewPrescription(data)}
          />
          {/* <RiDeleteBin5Fill
            className="ms-2"
            style={{ color: "#C9273A" }}
            size={18}
            onClick={() => {
              setDeleteId(cell?.prescriptionId);
              setShowDeleteModal(true);
            }}
          /> */}
        </Link>
      ),
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
  ];

  const customTotal = (from, to, size) => (
    <span className="react-bootstrap-table-pagination-total">
      {from} to {to} out of {size} entries
    </span>
  );

  const paginateOptions = {
    paginationSize: 3,
    pageStartIndex: 1,
    alwaysShowAllBtns: true,
    withFirstAndLast: false,
    hideSizePerPage: true,
    showTotal: true,
    paginationTotalRenderer: customTotal,
    disablePageTitle: true,
    sizePerPageList: [
      {
        text: "3",
        value: 3,
      },
    ],
  };

  return (
    <>
      <Card
        className="mb-0 payment-dashboard-table"
        style={{ height: "390px" }}
      >
        <Card.Body className="p-0">
          <Row className="px-4 pt-3">
            <Col md={4} className="d-flex align-items-center">
              <Card.Title>Current Prescription</Card.Title>
            </Col>
            <Col
              md={8}
              className="d-flex flex-wrap align-self-center justify-content-end"
            >
              <span className="d-flex justify-content-between align-self-center">
                <Form.Control
                  onKeyDown={(e) => {
                    if (e.key === "Enter") {
                      setSearchValue(e.target.value);
                    }
                  }}
                  onChange={(e) => {
                    if (e.key === "Enter") {
                      setSearchValue(e.target.value);
                    }
                  }}
                  type="text"
                  placeholder="Search"
                  className="search-field-spacing mb-3"
                  aria-label="Search"
                />
                <BiSearch size={22} className="searchbar-icon" />
              </span>
              <Button
                variant="outline-secondary"
                className="custom-outline-btn mb-3"
                onClick={() => navigate(PATH.PATIENT_PRESCRIPTION)}
              >
                View All
              </Button>
            </Col>
          </Row>
          <span className="patient-datatable">
            <BootstrapTable
              columns={columns}
              data={
                patientApt?.allPrescription ? patientApt?.allPrescription : []
              }
              keyField="prescriptionId"
              id="prescriptionId"
              pagination={paginationFactory(paginateOptions)}
              // striped
              bordered={false}
              wrapperClasses="table-responsive"
            />
          </span>
        </Card.Body>
      </Card>
      <MedicationModel
        viewPresctionData={viewPresctionData}
        handleClose={handleClose}
        onDownload={printTimeOut}
        show={show}
      />
      <PrintComponents
        trigger={
          <button
            ref={printButtonRef}
            className="border-0 px-3 py-2 rounded-2 d-none"
          >
            <PiDownloadSimpleBold size={20} /> Download PDF
          </button>
        }
      >
        <PrescriptionPDF prescriptionData={viewPresctionData} />
      </PrintComponents>
      {/* <ConfirmationModal
        show={showDeleteModal}
        onHide={() => setShowDeleteModal(false)}
        heading={"Delete Prescription"}
        title={"the prescription"}
        removeFunc={deletePres}
      /> */}
    </>
  );
}
