import React, { useEffect } from "react";
import { Card, Row, Col } from "react-bootstrap";
import IMAGES from "../../../assets/images";
import CurrentPrescriptionDatatable from "./CurrentPrescriptionDataTable";
import Calendar from "./Calendar";
import PaymentInfoDatatable from "./PaymentInfoDataTable";
import { getTotalAppointments } from "../../../app/features/patientDashboard/patientDashboard.slice";
import { useDispatch, useSelector } from "react-redux";

export default function Dashboard() {
  const dispatch = useDispatch();
  const patientApt = useSelector((state) => state?.patientDashboard);

  useEffect(() => {
    dispatch(getTotalAppointments());
  }, [dispatch]);


  return (
    <>
      <Row className="main-row patient-dashbaord">
        <Col sm={6} xs={12} className="xs-margin-bottom">
          <Card className="h-100">
            <Card.Body className="d-flex justify-content-between align-items-center p-4 display-column-reverse">
              <span>
                <Card.Title>Total Appointments</Card.Title>
                <h3 className="mb-0">{patientApt?.totalAppointments?.appointmentCount}</h3>
              </span>
              <img src={IMAGES.APPOINTMENT_ICON} alt="appointment icon" className="color-dk-blue"/>
            </Card.Body>
          </Card>
        </Col>
        <Col sm={6} xs={12} className="xs-margin-bottom">
          <Card className="h-100">
            <Card.Body className="d-flex justify-content-between align-items-center p-4 display-column-reverse">
              <span>
                <Card.Title>Upcoming Appointments</Card.Title>
                <h3 className="mb-0">{patientApt?.totalAppointments?.upcomingAppointmentCount}</h3>
              </span>
              <img src={IMAGES.APPOINTMENT_ICON} alt="new patient icon" className="color-dk-blue"/>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      <Row className="my-3">
        <Col xl={8} xs={12}>
          <Row>
            <Col xs={12} className="mb-3">
              <CurrentPrescriptionDatatable />
            </Col>
            <Col xs={12} className="mb-3">
              <PaymentInfoDatatable />
            </Col>
          </Row>
        </Col>
        <Col xl={4} xs={12}>
          <Row>
            <Col xs={12}>
              <Calendar />
            </Col>
          </Row>
        </Col>
      </Row>
    </>
  );
}
